<template>
	<div>
		<navbar/>
		<signin-modal title="Select the method to authenticate" closebutton="true">
			<div slot="content">
				<center>
					<signin-google-button></signin-google-button>
				</center>
			</div>
		</signin-modal>
		<div class="hero is-fullheight-with-navbar principalHero-presentation">
			<div class="hero-body">
				<div class="container has-text-centered">
					<p class="title is-3 has-text-white">
						MathQuestionary
					</p>
					<p class="subtitle is-4 has-text-white">
						 Entrena tu mente y aumenta tus ganas de aprender !
					</p>
					<toast color="is-danger" v-show="false" :timeout="10000" outclass="slower zoomOut" inclass="faster slideInDown" :buttonclose="true">
						<div slot="content">
							<h2>Hello world</h2>
						</div>
					</toast>
				</div>
			</div>
		</div>
		<div class="hero is-fullheight famous-hero">
			<div class="hero-body coders-container">
				<div class="container">
					<div class="columns is-multiline">
						<participantComponent v-for="(participante,index) in participantes" :key="index" col="4" :name="participante.name" :image="participante.image" :coder="participante.coder"/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import '@/assets/css/MathQuestionary.guest.css';
	import signInGoogleButton from '@/components/signIn.google.vue';
	import signInModal from '@/components/signIn.modal.vue';
	import navbar from '@/components/navbar.vue';
	import toast from '@/components/toast.mathquestionary.vue';
	import participantComponent from '@/components/ParticipantComponent';
	import firebase from 'firebase';
	import ParticipantesData from '@/data/participants.data';
	import {db} from '@/main';
	export default{
		components:{participantComponent,toast,navbar,'signin-modal':signInModal,'signin-google-button' : signInGoogleButton},
		data:()=>({
			participantes: []
		}),
		mounted()
		{
			this.participantes = ParticipantesData;
			var thisObj = this;
			firebase.auth().onAuthStateChanged(function(user) {
			  if (user) {
			    // User is signed in.
			    var displayName = user.displayName;
			    var email = user.email;
			    var emailVerified = user.emailVerified;
			    var photoURL = user.photoURL;
			    var isAnonymous = user.isAnonymous;
			    var uid = user.uid;
			    var providerData = user.providerData;
			    var userRef = db.collection('users').doc(uid);
			    var data = {};
			    userRef.get().then((user)=>{
			    	data = user.data();
					thisObj.$store.commit('setUser', data);
				    thisObj.$router.push('/member')
			    });
			  } else {
			    // User is signed out.
			    // ...
			  }
			});
			document.querySelector('body,html').style['overflow-y']='scroll';

		},
		methods:{
			openSignIn(){
				this.$store.commit('setModalState',{signIn: true,register:false});
			}
		}
	}
</script>
<style>
	
</style>


