<template>
	<div class="column" :class="col != null ? 'is-'+col : ''">
		<div class="card participantComponent" :class="col != null ? 'is-'+col : ''">
		  <div class="card-image">
		    <figure class="image">
		      <img :src="image" alt="Placeholder image">
		    </figure>
		  </div>
		  <div class="card-content">
		    <div class="media">
		      <div class="media-content has-text-centered">
		        <p class="title is-4 has-text-centered">{{ name }}</p>
		        <span class="tag is-primary is-medium" v-if="coder">
		        	CODER
		        </span>
		        <span class="tag is-info is-medium" v-else>
		        	QUESTION CREATOR
		        </span>
		      </div>
		    </div>
		  </div>
		</div>
	</div>
</template>
<script>
	export default{
		props:['col','name','image','coder'],
		data:()=>({

		})
	}
</script>


